/* IndexPage.css */

.container {
  text-align: center;
  font-family: Arial, sans-serif;
  margin-top: 50px;
}

h1 {
  font-size: 2.5rem;
  color: #333;
  margin-bottom: 30px;
}

button {
  padding: 12px 25px;
  font-size: 1rem;
  margin: 10px;
  cursor: pointer;
  border: none;
  border-radius: 4px;
  transition: background-color 0.3s;
}

button:hover {
  background-color: #45a049;
  color: white;
}

button:focus {
  outline: none;
}

button.login {
  background-color: #4CAF50;
  color: white;
}

button.signup {
  background-color: #2196F3;
  color: white;
}
