/* CreateQuizPage.css */

.container {
  max-width: 900px;
  margin: 0 auto;
  padding: 20px;
  font-family: Arial, sans-serif;
}

h2 {
  text-align: center;
  color: #333;
  font-size: 2rem;
  margin-bottom: 30px;
}

form {
  background-color: #f9f9f9;
  padding: 20px;
  border-radius: 8px;
  box-shadow: 0 2px 8px rgba(0, 0, 0, 0.1);
}

div {
  margin-bottom: 15px;
}

label {
  font-size: 1rem;
  font-weight: bold;
  display: block;
  margin-bottom: 5px;
}

input[type="text"],
input[type="number"],
select,
button {
  width: 100%;
  padding: 8px;
  margin-top: 5px;
  border: 1px solid #ccc;
  border-radius: 4px;
  font-size: 1rem;
}

input[type="text"],
input[type="number"] {
  height: 36px;
}

select {
  height: 40px;
}

button {
  background-color: #4CAF50;
  color: white;
  cursor: pointer;
  border: none;
  margin-top: 10px;
  transition: background-color 0.3s ease;
}

button:hover {
  background-color: #45a049;
}

h3 {
  font-size: 1.5rem;
  margin-bottom: 15px;
  color: #444;
}

.question-container {
  margin-bottom: 20px;
  padding: 15px;
  background-color: #f1f1f1;
  border-radius: 8px;
  border: 1px solid #ddd;
}

.question-container h4 {
  margin-top: 0;
}

.question-container input[type="text"] {
  width: calc(100% - 16px); /* To make space for padding */
}

.option-container {
  margin-bottom: 10px;
}

button[type="button"] {
  background-color: #f44336;
  border: none;
  color: white;
  padding: 8px 16px;
  margin-top: 10px;
  cursor: pointer;
  border-radius: 4px;
}

button[type="button"]:hover {
  background-color: #d32f2f;
}

.upload-container {
  margin-top: 20px;
}

.upload-container input {
  padding: 8px;
}

button[type="submit"] {
  background-color: #2196F3;
  margin-top: 20px;
}

button[type="submit"]:hover {
  background-color: #1976D2;
}
