/* src/ManageQuizPage.css */
input {
  padding: 8px;
  margin-right: 10px;
  margin-bottom: 20px;
}

button {
  padding: 8px 12px;
  cursor: pointer;
}

button:hover {
  background-color: #007bff;
  color: white;
}

ul {
  list-style-type: none;
  padding: 0;
}

li {
  padding: 8px;
  background-color: #f8f9fa;
  margin-bottom: 8px;
}

/* Modal */
.modal {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.5); /* Màu nền mờ */
  display: flex;
  justify-content: center;
  align-items: center;
  z-index: 9999; /* Đảm bảo modal nằm trên các phần tử khác */
}

.modal-content {
  background-color: white;
  padding: 20px;
  border-radius: 5px;
  width: 400px;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
}

.modal h3 {
  text-align: center;
  margin-bottom: 20px;
}

.modal label {
  display: block;
  margin-bottom: 8px;
}

.modal input {
  width: 100%;
  padding: 10px;
  margin-bottom: 20px;
  border: 1px solid #ccc;
  border-radius: 5px;
}

.modal button {
  padding: 10px 20px;
  background-color: #4CAF50;
  color: white;
  border: none;
  border-radius: 5px;
  cursor: pointer;
}

.modal button:hover {
  background-color: #45a049;
}

.modal button + button {
  background-color: #f44336;
}

.modal button + button:hover {
  background-color: #da190b;
}
